// ==================      exportGif FUNCTIONALITY      ================== //

function prepareForGif() {
  const frameImages = [];
  for (let i = 0; i < framesUrls.length; i++) {
    const canvasGif = document.createElement('canvas');
    canvasGif.width = canvasCurrVirtualSize;
    canvasGif.height = canvasCurrVirtualSize;
    const ctxGif = canvasGif.getContext('2d');
    ctxGif.fillStyle = 'black';
    ctxGif.fillRect(0, 0, canvasCurrVirtualSize, canvasCurrVirtualSize);
    const frameSrc = framesUrls[i];
    const frame = new Image();
    frame.src = frameSrc;
    ctxGif.drawImage(frame, 0, 0);
    const frameSrc2 = canvasGif.toDataURL('image/png');
    // const frameSrc2 = framesUrls[i];
    const frame2 = new Image();
    frame2.src = frameSrc2;
    frameImages[i] = frame2;
  }
  return frameImages;
}

exportGif.onclick = () => {
  gifshot.createGIF({
    images: prepareForGif(),
    interval: 1 / speedRange.value,
    // gifWidth: canvasCurrVirtualSize, // break colors - gifshot full of bugs
    // gifHeight: canvasCurrVirtualSize, // break colors - gifshot full of bugs
  }, (obj) => {
    if (!obj.error) {
      const imageTemp = obj.image;

      const linkToSave = document.createElement('a');
      linkToSave.setAttribute('download', 'piskelGIF.gif');
      linkToSave.setAttribute('href', imageTemp);
      linkToSave.click();
    }
  });
};

function encodeApng() {
  const canvasApng = document.createElement('canvas');
  canvasApng.width = canvasCurrVirtualSize;
  canvasApng.height = canvasCurrVirtualSize;
  const encoder = new APNGencoder(canvasApng);
  encoder.start();
  encoder.setRepeat(0);
  encoder.setDelay(100 / speedRange.value);
  encoder.setDispose(1);
  encoder.setBlend(1);
  for (let i = 0; i < framesHTML.children.length - 1; i++) {
    const myImageData = canvasCur(i).getContext('2d').getImageData(0, 0, canvasCurrVirtualSize, canvasCurrVirtualSize);
    canvasApng.getContext('2d').putImageData(myImageData, 0, 0);
    encoder.addFrame(canvasApng.getContext('2d'));
  }
  encoder.finish();

  const out = encoder.stream();
  return new Blob([new Uint8Array(out.bin)], { type: 'image/apng' });
}

exportApng.onclick = () => {
  const linkToSave = document.createElement('a');
  linkToSave.href = URL.createObjectURL(encodeApng());
  linkToSave.download = 'piskelAPNG.apng';
  linkToSave.click();
};

