/* eslint-disable no-param-reassign */
// eslint-disable-next-line max-len
export default function rectangleAction(showCursorCoords, isMouseDown, toolState, ctxMain, canvasMain, canvasToCursor, ctxToCursor, ctxPreview, drawLine, copyCanvas, canvasCur, returnCurrentFrame) {
  let colorToPaint;
  let x0;
  let y0;
  let prevX;
  let prevY;

  canvasToCursor.addEventListener('contextmenu', (e) => {
    e.preventDefault();
  });

  canvasToCursor.addEventListener('mousedown', (e) => {
    if (toolState.currentToolId === 'rectangle') {
      isMouseDown = true;
      x0 = e.offsetX;
      y0 = e.offsetY;
      if (e.button === 2) colorToPaint = toolState.secondaryColor;
      else colorToPaint = toolState.primaryColor;
    }
  });

  canvasToCursor.addEventListener('mouseup', (e) => {
    if (toolState.currentToolId === 'rectangle') {
      ctxToCursor.beginPath();
      ctxToCursor.clearRect(0, 0, canvasMain.width, canvasMain.height);
      ctxToCursor.fill();
      isMouseDown = false;
      ctxMain.beginPath();
      if (e.button === 2) colorToPaint = toolState.secondaryColor;
      else colorToPaint = toolState.primaryColor;
      ctxMain.fillStyle = colorToPaint;
      drawLine(x0, y0, e.offsetX, y0, ctxMain, false);
      drawLine(x0, y0, x0, e.offsetY, ctxMain, false);
      drawLine(e.offsetX, y0, e.offsetX, e.offsetY, ctxMain, false);
      drawLine(x0, e.offsetY, e.offsetX, e.offsetY, ctxMain, false);
      copyCanvas(canvasMain, canvasCur(returnCurrentFrame()));
    }
  });

  canvasToCursor.addEventListener('mouseout', () => {
    if (toolState.currentToolId === 'rectangle') {
      ctxToCursor.beginPath();
      ctxToCursor.clearRect(prevX, prevY, 1, 1);
      ctxToCursor.fill();
      showCursorCoords();
    }
  });

  canvasToCursor.addEventListener('mousemove', (e) => {
    showCursorCoords(e.offsetX, e.offsetY);
    if (toolState.currentToolId === 'rectangle') {
      if (isMouseDown === true) {
        ctxToCursor.beginPath();
        drawLine(x0, y0, prevX, y0, ctxToCursor, true);
        drawLine(x0, y0, x0, prevY, ctxToCursor, true);
        drawLine(prevX, y0, prevX, prevY, ctxToCursor, true);
        drawLine(x0, prevY, prevX, prevY, ctxToCursor, true);
        prevX = e.offsetX;
        prevY = e.offsetY;
        ctxToCursor.fillStyle = colorToPaint;
        drawLine(x0, y0, e.offsetX, y0, ctxToCursor, false);
        drawLine(x0, y0, x0, e.offsetY, ctxToCursor, false);
        drawLine(e.offsetX, y0, e.offsetX, e.offsetY, ctxToCursor, false);
        drawLine(x0, e.offsetY, e.offsetX, e.offsetY, ctxToCursor, false);
      }
      if (isMouseDown === false) {
        ctxToCursor.beginPath();
        ctxToCursor.clearRect(prevX, prevY, 1, 1);
        ctxToCursor.fill();
        prevX = e.offsetX;
        prevY = e.offsetY;
        ctxToCursor.fillStyle = 'rgba(255,255,255, 0.5)';
        ctxToCursor.fillRect(e.offsetX, e.offsetY, 1, 1);
      }
    }
  });
}
