/* eslint-disable max-len */
/* eslint-disable no-param-reassign */
// eslint-disable-next-line max-len
export default function paintcanvasAction(showCursorCoords, isMouseDown, toolState, ctxMain, canvasMain, canvasToCursor, ctxToCursor, ctxPreview, drawLine, copyCanvas, canvasCur, returnCurrentFrame) {
  let prevX;
  let prevY;
  let colorToPaint;

  canvasToCursor.addEventListener('contextmenu', (e) => {
    e.preventDefault();
  });

  canvasToCursor.addEventListener('mousedown', (e) => {
    if (toolState.currentToolId === 'paintcanvas') {
      ctxToCursor.clearRect(prevX, prevY, 1, 1);
      isMouseDown = true;
      ctxMain.beginPath();
      if (e.button === 2) colorToPaint = toolState.secondaryColor;
      else colorToPaint = toolState.primaryColor;
      ctxMain.fillStyle = colorToPaint;
      ctxMain.fillRect(0, 0, canvasMain.width, canvasMain.height);
    }
  });
  canvasToCursor.addEventListener('mouseup', () => {
    if (toolState.currentToolId === 'paintcanvas') {
      isMouseDown = false;
      ctxMain.beginPath();
      copyCanvas(canvasMain, canvasCur(returnCurrentFrame()));
    }
  });

  canvasToCursor.addEventListener('mouseout', () => {
    if (toolState.currentToolId === 'paintcanvas') {
      ctxToCursor.beginPath();
      ctxToCursor.clearRect(prevX, prevY, 1, 1);
      ctxToCursor.fill();
      showCursorCoords();
    }
  });

  canvasToCursor.addEventListener('mousemove', (e) => {
    if (toolState.currentToolId === 'paintcanvas') {
      showCursorCoords(e.offsetX, e.offsetY);
      if (isMouseDown === false) {
        ctxToCursor.beginPath();
        ctxToCursor.clearRect(prevX, prevY, 1, 1);
        ctxToCursor.fill();
        prevX = e.offsetX;
        prevY = e.offsetY;
        ctxToCursor.fillStyle = 'rgba(255,255,255, 0.5)';
        ctxToCursor.fillRect(e.offsetX, e.offsetY, 1, 1);
      }
    }
  });
}
